html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}
html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  font-feature-settings: 'tnum' on, 'lnum' on;
  font-family: Helvetica, Arial, sans-serif;
  color: #000000;
}
body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}
a {
  color: #000000;
  text-decoration: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}
input {
  border-radius: 0;
}
input::placeholder {
  opacity: 1;
}
input:invalid {
  box-shadow: none;
}
select {
  border-radius: 0;
}
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden,
input[type="text"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 40px;
}
.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.main-nav__item {
  align-self: flex-end;
  margin-right: 30px;
  font-family: Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
}
.main-nav__item--logo {
  margin-right: 64px;
  margin-left: 34px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  color: #9F9F9F;
}
.main-nav__toggle {
  display: none;
}
@media (max-width: 768px) {
  .main-nav__toggle {
    display: block;
  }
}
@media (max-width: 768px) {
  .main-nav--opened .main-nav__list {
    display: block;
  }
}
@media (max-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.header {
  padding-top: 30px;
  padding-bottom: 50px;
}
.header__logo {
  grid-area: logo;
}
.header__logo svg {
  width: 160px;
  height: 50px;
}
.header__top {
  display: flex;
  width: 100%;
}
.user-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: flex-end;
  margin-left: auto;
}
.user-menu__item {
  position: relative;
  margin-right: 20px;
}
.user-menu__item:last-child {
  margin-right: 0;
}
.user-menu__count {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 16px;
  height: 16px;
  top: 0;
  right: 0;
  background-color: #000000;
  color: #ffffff;
  border-radius: 50%;
  font-size: 12px;
  line-height: 14px;
}
.breadcrumbs {
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  list-style: none;
  display: flex;
}
.breadcrumbs__item {
  position: relative;
  margin-right: 26px;
  color: #9F9F9F;
}
.breadcrumbs__item::after {
  position: absolute;
  content: '/';
  top: 0;
  right: 0;
  margin-right: -16px;
  color: #9F9F9F;
}
.breadcrumbs__item .breadcrumbs__link {
  color: #9F9F9F;
}
.breadcrumbs__item-current {
  margin-right: 0;
  color: #000000;
}
.breadcrumbs__item-current::after {
  position: absolute;
  content: none;
}
.title {
  width: 100%;
  margin: 0;
  color: inherit;
}
.title--h1 {
  font-weight: 700;
  font-size: 40px;
  line-height: 46px;
}
.title--h2 {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
}
.title--h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
}
.products__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.products__item {
  display: grid;
  grid-template: 'img title price' 'img description price' 'img count delete';
  grid-template-columns: 150px 180px 100px;
  margin-bottom: 50px;
  position: relative;
}
.products__item::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  margin-bottom: -25px;
  width: 100%;
  height: 1px;
  background-color: #DEDEDE;
}
.products__item:last-child {
  margin-bottom: 0;
}
.products__item:last-child::after {
  content: none;
}
.products__img {
  grid-area: img;
}
.products__title {
  grid-area: title;
}
.products__description {
  grid-area: description;
}
.products__count {
  grid-area: count;
}
.products__price {
  grid-area: price;
}
.products__btn-del {
  grid-area: delete;
}
.form-search {
  display: flex;
  align-self: baseline;
}
.form-search label {
  display: flex;
  margin-right: 20px;
}
.form-search label svg {
  width: 25px;
  height: 25px;
}
.form-search input {
  outline: 0;
  width: 250px;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  font-family: Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  font-feature-settings: 'tnum' on, 'lnum' on;
  color: #000000;
  background-color: #ffffff;
}
.form-search input::placeholder {
  color: #9F9F9F;
}
